.skin-blue .checkbox {
  margin-top: 0;
  margin-bottom: 0;
  padding: 0px;
}

.skin-blue label {
  display: inline-block;
  max-width: 100%;
  margin-bottom: 9px;
  /* font-weight: 700 !important; */
}

.skin-blue .radio {
  margin-top: 0;
  margin-bottom: 0;
  padding: 0px;
}

.skin-blue .checkbox>label {
  position: relative;
  cursor: pointer;
  margin-right: 10px;
  padding-left: 25px;
}

.skin-blue .radio>label {
  position: relative;
  padding-left: 25px;
  margin-right: 10px;
  color: #666666;
  width: 20px;
}

.spaninline {
  display: inline-block;
  vertical-align: middle;
}

li .inline {
  display: inline-block;
  vertical-align: text-top;
}

tr ul li {
  line-height: 30px;
}

.operateion {
  margin-left: 15px;
  vertical-align: text-top;
}

.cellTitle {
  width: 100px;
  text-align: center !important;
}
